    headThFullnameDescr.innerText = '–ê-–Ø';
    headThCreatedate.setAttribute('id', 'createdAt');
    headThCreatedateTitle.innerText = '–î–∞—Ç–∞ –∏ –≤—Ä–µ–º—è —Å–æ–∑–¥–∞–Ω–∏—è';
    headThUpdatedate.setAttribute('id', 'updatedAt');
    headThUpdatedateTitle.innerText = '–ü–æ—Å–ª–µ–¥–Ω–∏–µ –∏–∑–º–µ–Ω–µ–Ω–∏—è';
    headThContacts.setAttribute('id', 'contacts');
    headThContacts.innerText = '–ö–æ–Ω—Ç–∞–∫—Ç—ã';
    headThActions.setAttribute('id', 'actions');
